.App {
  text-align: center;
  //background: rgba(255, 100, 100,1); // orange red 3
  //background: #59AFFF; // light blue 1
  //background: #D1C300; // yellow 2
  background: transparent; // deepblue 4
}
.prevVideoBtn{
  width: 6%;
  position: fixed;
  z-index: 21;
  left: 20px;
  bottom:30%;
  cursor: pointer;
}
.nextVideoBtn{
  width: 6%;
  position: fixed;
  z-index: 21;
  right: 20px;
  bottom:30%;
  cursor: pointer;
}
.hideVideoBtn{
  width:50px;
  position: fixed;
  top: 33px;
  right: 30px;
  cursor: pointer;
  z-index: 22;
}
.audioBtn{
  width:50px;
  position: fixed;
  top: 145px;
  right: 30px;
  cursor: pointer;
  z-index: 22;
}
.stopVideoBtn{
  width:50px;
  position: fixed;
  top: 385px;
  right: 30px;
  cursor: pointer;
  z-index: 22;
}
.changeSpeedBtn{
  width:50px;
  position: fixed;
  top: 265px;
  right: 30px;
  cursor: pointer;
  z-index: 22;
}
.aboutBtn{
  width: 18px;
  position: fixed;
  bottom: 40px;
  right: 25%;
  cursor: pointer;
  z-index: 22;
}
svg{
  overflow: initial;
}
.logo{
  width: 46%;
  position: fixed;
  left: 24px;
  bottom: 24px;
  z-index: 20;
}
#happyday{
  position: fixed;
  top:0;
  left: 0;
  z-index: 10; 
  backdrop-filter: blur(3px);
}
#myVideo{
  position: fixed;
  top:0;
  left: 0;
  z-index: 1;

}
body{
  //background: rgba(255, 100, 100,1);
  background: #59AFFF;
  //background: #D1C300; // yellow 2
  //background: #4A1DEA; // deepblue 4
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.bgTitles{
  width:100%;
  height:100%;
  position:absolute;
  top:0px;
  left:0px;
  display: none;
}

@media (max-width: 720px) {

  .prevVideoBtn{
    width: 15%;
    left: 20px;
    bottom:20%;
  }

  .nextVideoBtn{
    width: 15%;
    right: 20px;
    bottom:20%;
  }
  .aboutBtn{
    width: 9px;
    bottom: 30px;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
